.app-header {
    height: 70px;
    margin-inline: rem(70px);
    display: flex;
    align-items: center;
    justify-content: flex-end;
    background-color: rgba($color: white, $alpha: 0.5);
    border-radius: 10px;

    @include for-mobile-layout {
        margin-inline-start: 0;
        margin-inline-end: 0;
        // margin-inline-end: rem(15px);
    }
}
