.home-page {
    background-color: rgba($color: white, $alpha: 0.5);
    display: flex;
    flex-direction: column;
    gap: rem(40px);
    width: 90vw;

    .guest-welcome {
        display: flex;
        flex-direction: column;
        gap: rem(10px);
    }

    .user-balance {
        display: flex;
        flex-direction: column;
        gap: rem(10px);
    }

    &.logged-user {
        text-align: center;
        .MuiTable-root {
            background-color: aliceblue;
        }
    }

    button {
        width: fit-content;
    }
}
